import { useState } from 'react';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import './App.css';
import Navbar from './components/layout/Navbar';
import Users from './components/users/Users';
import { get } from 'axios';
import Search from './components/users/Search';
import Alert from './components/layout/Alert';
import About from './components/pages/About';
import SingleUser from './components/users/SingleUser';

function App() {
  let initialState = {
    loading: false,
    users: [],
    user: {},
    repos: [],
    alert: null,
  };
  const [state, setState] = useState(initialState);

  let { loading, users, user, repos, alert } = state;

  const searchUsers = async (text) => {
    setState((state) => ({ ...state, loading: true }));
    let response = await get(
      `https://api.github.com/search/users?q=${text}&client_id=${process.env.REACT_APP_GITHUB_CLIENT_ID}&client_secret=${process.env.REACT_APP_GITHUB_CLIENT_SECRET}`
    );
    setState((state) => ({
      ...state,
      loading: false,
      users: response.data.items,
    }));
  };

  const getUser = async (username) => {
    setState((state) => ({ ...state, loading: true }));
    let response = await get(
      `https://api.github.com/users/${username}?client_id=${process.env.REACT_APP_GITHUB_CLIENT_ID}&client_secret=${process.env.REACT_APP_GITHUB_CLIENT_SECRET}`
    );
    setState((state) => ({
      ...state,
      loading: false,
      user: response.data,
    }));
  };

  const getUserRepos = async (username) => {
    setState((state) => ({ ...state, loading: true }));
    let response = await get(
      `https://api.github.com/users/${username}/repos?per_page=5&sort=created:asc&client_id=${process.env.REACT_APP_GITHUB_CLIENT_ID}&client_secret=${process.env.REACT_APP_GITHUB_CLIENT_SECRET}`
    );
    setState((state) => ({
      ...state,
      loading: false,
      repos: response.data,
    }));
  };

  const clearUsers = () => setState(initialState);

  const setAlert = (msg, style) => {
    !msg
      ? setState((state) => ({ ...state, alert: null }))
      : setState((state) => ({ ...state, alert: { msg, style } }));
  };

  return (
    <Router>
      <div className="App">
        <Navbar icon="fab fa-github">GitHub Finder</Navbar>
        <div className="container">
          <Alert alert={alert} />
          <Switch>
            <Route
              path="/"
              exact
              render={() => (
                <>
                  <Search
                    searchUsers={searchUsers}
                    clearUsers={clearUsers}
                    showClear={users.length !== 0 ? true : false}
                    setAlert={setAlert}
                  />
                  <Users loading={loading} users={users} />
                </>
              )}
            />
            <Route exact path="/about" component={About} />
            <Route
              exact
              path="/user/:login"
              render={(props) => (
                <SingleUser
                  {...props}
                  getUser={getUser}
                  getUserRepos={getUserRepos}
                  user={user}
                  repos={repos}
                  loading={loading}
                />
              )}
            />
          </Switch>
        </div>
      </div>
    </Router>
  );
}

export default App;
